import classNames from "classnames";
import { Container, Row, Col, Collapse } from "react-bootstrap";

import { AppContext } from "index.js";

function Qa({ id, q, a }) {
  const [open, setOpen] = useState(false);
  const onQaClick = function () {
    if (open) return;
    window.dataLayer.push({
      event: "faq-open",
      faq: id,
    });
    setOpen(true);
  };

  return (
    <div
      className={classNames(styles.qa, "mx-n3 mx-md-0 mb-3 svg-shadow")}
      onClick={onQaClick}
      aria-controls={id}
      aria-expanded={open}
    >
      <p className={styles.q}>{q}</p>
      <Collapse in={open}>
        <div id={id} className={styles.a}>
          {a}
        </div>
      </Collapse>
    </div>
  );
}

